@import "./Variables.scss";
.slider{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    .slider-block{
        height: 500px;
        width: 60%;
        display: flex;
        margin: auto;
        padding: 50px;
        margin-top: -60px;
        margin-bottom: 50px;
        margin-right: 450px;
        border-bottom: 8px solid white;
        border-radius: 10%;
        .slider-block__left-icon{
            font-size: 3em;
            color: $third-text-color;
            margin: auto;
            margin-top: 200px;
            margin-right: 20px;
            cursor: pointer;
        }
        .slider-block__right-icon{
            font-size: 3em;
            color: $third-text-color;
            margin: auto;
            margin-top: 200px;
            margin-left: 20px;
            cursor: pointer;
        }
        .slider-block__slider-text{
            display: flex;
            flex-direction:column;
            justify-content: flex-start;
            align-items: center;
            margin: auto;
            margin-bottom: 150px;
        }
        .slider-text__slider-title{
            font-family: $main-font-familiy;
            font-size: 3em;
            color: $third-text-color;
            text-shadow: 0px 4px 3px rgba(0,0,0,0.4),
                         0px 8px 13px rgba(0,0,0,0.1),
                         0px 18px 23px rgba(0,0,0,0.1);
            text-align: center;
        }
        .slider-text__slider-description{
            width: 90%;
            font-family: $secondary-font-family;
            font-size: 2em;
            color: white;
            text-align: center;
            margin: auto;
        }
        .logos{
            margin-left: 50px;
            margin-top: 20px;
        }
    }
}